// =============================================================================
// NEXT.JS INSTRUMENTATION
// =============================================================================
// This file runs once when the Next.js server starts
// Used to:
// 1. Validate environment configuration (fail fast in production)
// 2. Initialize background jobs like facilitator probes

export async function register() {
  if (process.env.NEXT_RUNTIME === 'nodejs') {
    // Only run in Node.js runtime (not Edge)
    
    // 1. Run production environment validation
    try {
      const { runStartupValidation } = await import('@/lib/startup/validate-env');
      runStartupValidation();
    } catch (error) {
      console.error('[FATAL] Startup validation failed:', error);
      // In production, this prevents the app from starting with bad config
      if (process.env.NODE_ENV === 'production') {
        throw error;
      }
    }
    
    // 2. Determine if we should start probes
    const env = process.env.NODE_ENV || 'development';
    const enableProbes = process.env.ENABLE_FACILITATOR_PROBES === 'true';
    const isProduction = env === 'production';
    const isTest = env === 'test';
    
    // Probes only run in production when explicitly enabled
    // Never run in local dev (next dev) or test environments
    if (isTest) {
      console.log('[SMF] Probes disabled: test environment');
      return;
    }
    
    if (!isProduction) {
      console.log('[SMF] Probes disabled: not in production environment');
      console.log(`[SMF] Current NODE_ENV: ${env}`);
      console.log('[SMF] Probes will run in production when ENABLE_FACILITATOR_PROBES=true is set');
      return;
    }
    
    if (!enableProbes) {
      console.log('[SMF] Probes disabled: ENABLE_FACILITATOR_PROBES not set to "true"');
      console.log('[SMF] Set ENABLE_FACILITATOR_PROBES=true in Vercel environment variables to enable');
      return;
    }
    
    // Production + ENABLE_FACILITATOR_PROBES=true: Probes are now handled by Vercel Cron
    // The old setInterval-based probes are deprecated in favor of cron-based probes
    // Cron endpoint: /api/cron/facilitator-probes (runs every minute)
    console.log('[SMF] ========================================');
    console.log('[SMF] Facilitator probes are now handled by Vercel Cron');
    console.log(`[SMF] Environment: ${env}`);
    console.log(`[SMF] ENABLE_FACILITATOR_PROBES: ${enableProbes}`);
    console.log('[SMF] Cron endpoint: /api/cron/facilitator-probes');
    console.log('[SMF] Cron schedule: Every minute (* * * * *)');
    console.log('[SMF] Probe intervals enforced in code (30s for Base, 2m for Solana)');
    console.log('[SMF] Check /api/v1/smf/probes/status for probe status');
    console.log('[SMF] ========================================');
  }
}
